"use client";

import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { User, ShoppingCart, Heart, ClipboardList, MapPin, X, Menu } from "lucide-react";
import dynamic from "next/dynamic";
import { Button } from "@/components/ui/button";

// Lazy load pages to avoid SSR issues
const Wishlist = dynamic(() => import("../../wishlist/page"), { ssr: false });
const OrderPage = dynamic(() => import("../../orders/page"), { ssr: false });
const Cart = dynamic(() => import("../../cart/Cart"), { ssr: false });
const EditProfile = dynamic(() => import("../profile/manage/page"), { ssr: false });
const AddressManagement = dynamic(() => import("../profile/address/page"), { ssr: false });

const UserProfile = () => {
  const { user } = useSelector((state) => state.userData);
  const [activeSection, setActiveSection] = useState("profile");
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  // Ensure sidebar remains closed on mobile when resizing
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth >= 768) {
        setIsSidebarOpen(false);
      }
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <div className="flex min-h-screen bg-white relative">
      {/* Profile Icon Button for Mobile */}
      <button
        className="md:hidden fixed top-60 left-4 p-2 text-gray-800 bg-white rounded border z-50"
        onClick={() => setIsSidebarOpen(!isSidebarOpen)}
      >
        <User className="w-6 h-6" />
      </button>

      {/* Sidebar Navigation */}
      <nav
        className={`fixed md:relative w-64 bg-white text-gray-800 h-screen p-6 shadow-lg transition-transform duration-300 ease-in-out ${
          isSidebarOpen ? "translate-x-0" : "-translate-x-full"
        } md:translate-x-0 md:block z-40`}
      >
        {/* Close Button for Mobile */}
        <button
          className="md:hidden absolute top-4 right-4 p-2 text-gray-800"
          onClick={() => setIsSidebarOpen(false)}
        >
          <X className="w-5 h-5" />
        </button>

        {/* User Info */}
        <div className="flex items-center mb-6">
          <div className="w-12 h-12 rounded-full overflow-hidden bg-gray-300 flex items-center justify-center">
            {user?.profile_pic ? (
              <img src={user.profile_pic} alt="Profile" className="w-full h-full object-cover" />
            ) : (
              <span className="text-2xl font-bold text-gray-600">{user?.name?.charAt(0)}</span>
            )}
          </div>
          <h2 className="ml-2 text-lg font-semibold text-gray-700">{user?.name}</h2>
        </div>

        {/* Sidebar Menu */}
        <ul className="space-y-2">
          {[
            { label: "Edit Profile", icon: <User className="w-5 h-5 mr-2" />, section: "editProfile" },
            { label: "Wishlist", icon: <Heart className="w-5 h-5 mr-2" />, section: "wishlist" },
            { label: "Orders", icon: <ClipboardList className="w-5 h-5 mr-2" />, section: "orders" },
            { label: "Edit Address", icon: <MapPin className="w-5 h-5 mr-2" />, section: "editAddress" },
            { label: "Cart", icon: <ShoppingCart className="w-5 h-5 mr-2" />, section: "cart" },
          ].map(({ label, icon, section }) => (
            <li key={section}>
              <Button
                variant="ghost"
                className={`flex items-center w-full p-3 rounded-lg hover:bg-gray-100 text-gray-700 ${
                  activeSection === section ? "bg-gray-200" : ""
                }`}
                onClick={() => {
                  setActiveSection(section);
                  setIsSidebarOpen(false); // Close on mobile
                }}
              >
                {icon} {label}
              </Button>
            </li>
          ))}
        </ul>
      </nav>

      {/* Main Content */}
      <div className="flex-1 bg-gray-50 w-full overflow-y-scroll p-6">
        {activeSection === "profile" && <EditProfile />}
        {activeSection === "editAddress" && <AddressManagement />}
        {activeSection === "wishlist" && <Wishlist />}
        {activeSection === "orders" && <OrderPage />}
        {activeSection === "cart" && <Cart />}
      </div>
    </div>
  );
};

export default UserProfile;
